export const loadMore={
    directives: {
        'loadMore':{
            bind:(el,binding)=>{
                window.onscroll= function(){
                    console.log("触发");
                    //变量t是滚动条滚动时，距离顶部的距离
                    var t=document.documentElement.scrollTop||document.body.scrollTop;
                    binding.value(t);
                }
            }
        }
    }
}



