                p{
                    .sc(14px,#666);
                }
                .show_border{
                    border-left: 2px solid #f60;
                    border-bottom: 2px solid #f60;
                    border-right: 2px solid #f60;
                   // border: 2px solid #f60;
                    width: 80px;
                    position: absolute;
                    margin-left:-0.6rem;
                    top: 183px;
                    ul{
                        background: #fff;
                        li{
                           
                            padding: 0.3rem;
                            font-size:13px;
                        }
                        li:hover{
                            background: #f60;
                        }
                    }
                }
            }
    }
}
.main_container{
    background: #fff;
}
