        margin-top: 2px;
        span{
            font-size: 13px;
        }
        span:nth-of-type(1)
        {

        }
    }
    .right{
        ul{
            display: flex;
            justify-content: space-around;
            li{
                display: flex;
               align-items: center;
               justify-content: center;
               height: 30px;
               padding:0px 10px;
               text-align: center;
                font-size:13px;
                color:#666;
                
            }
            .hovers:hover{
                height: 30px;
                background: #fff;
            }
        }
    }
}
.zheyan{
    border:1px solid #ddd;
    position:absolute;
    left:268px;
    background: #fff;
    top: 100px;
    display: flex;
    flex-wrap: wrap;
    span{
        padding:5px;
        width: 162px;
    }
    span:hover{
        color:#f60;
    }
}
.el-dropdown-link{
    font-size: 13px;
    color:#666;
    display: flex;
    align-items: center;
}
.hos:hover{
    background: #f1f1f1;
}
</style>



